.expereince__container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}

.expereince__container > div{
    background: var(--color-bg-varient);
    padding:  2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.expereince__container > div:hover{
    background: transparent;
    border-color: var(--color-primary-varient);
    cursor: default;
}

.expereince__container > div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.expereince__content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
}

.expereince__details{
    display: flex;
    gap: 1rem;
}

.expereince__details-icon{
    margin-top: 6px;
    color: var(--color-primary);
}

/* MEDIA QUERY (MEDIUM DEVICES*/
@media screen and (max-width:1024px) {
  .expereince__container{
    grid-template-columns: 1fr;
  }
  .expereince__container > div{
    width: 80%;
    padding: 2rem;
    margin:  0 auto;
  }
  .expereince__content{
    padding: 1rem;
  }
}
/* MEDIA QUERY (MEDIUM DEVICES*/
@media screen and (max-width:600px) {
    .expereince__container{
        gap: 1rem;

    }
    .expereince__container > div{
        width: 100%;
        padding:  2rem 1rem;
    }
}