header{
    height: 100vh;
    padding-top: 7rem ;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}


/**CTA**/
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content:center ;
}

/**Socials**/

.header_socials {

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 6rem;
    bottom: 3rem;
}

.header_socials::after{
    content: "";
    width: 1px;
    height:  2rem;
    background: var(--color-primary);
     
}

/** ME**/

.me{

    background: var(--color-primary);
    height: 30rem;
    width: 22rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0 ;
    overflow: hidden;
    padding: rem  1.5rem 1.5rem 1.5rem;
    background: linear-gradient(var(--color-primary),transparent);
}

/**Scroll**/
.scroll_down{

    position: absolute;
    right: 3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}


/* MEDIA QUERY (MEDIUM DEVICES*/
@media screen and (max-width:1024px) {
    header {
       height: 60vh;
    }
}
/* MEDIA QUERY (MEDIUM DEVICES*/
@media screen and (max-width:600px) {
    header{
        height: 120vh;
    }

    .scroll_down,.header_socials{
        display: none;
    }
}